* {
  margin: 0;
  padding: 0;
  outline: 0;
  border: 0;
  text-decoration: none;
  list-style: none;
  background-color: transparent;
}

/*
    Remove all the styles of the "User-Agent-Stylesheet", except for the 'display' property
    - The "symbol *" part is to solve Firefox SVG sprite bug
 */
*:where(:not(iframe, canvas, img, svg, video):not(svg *, symbol *)) {
  all: unset;
  display: revert;
}

/* Preferred box-sizing value */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove list styles (bullets/numbers) */
ol, ul, menu {
  list-style: none;
}

/* For images to not be able to exceed their container */
img, video {
  display: block;
  max-width: 100%;
}

/* removes spacing between cells in tables */
table {
  border-collapse: collapse;
}

/* revert the 'white-space' property for textarea elements on Safari */
textarea {
  white-space: revert;
}

/* fix the feature of 'hidden' attribute.
   display:revert; revert to element instead of attribute */
:where([hidden]) {
  display: none;
}

/* revert for bug in Chromium browsers
   - fix for the content editable attribute will work properly. */
:where([contenteditable]) {
  -moz-user-modify: read-write;
  -webkit-user-modify: read-write;
  overflow-wrap: break-word;
  -webkit-line-break: after-white-space;
}

/* apply back the draggable feature - exist only in Chromium and Safari */
:where([draggable="true"]) {
  -webkit-user-drag: element;
}

/* Works on Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #4B3DDA #262837;
}

/* Works on Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: rem(12);
}

*::-webkit-scrollbar-track {
  background: #262837;
}

*::-webkit-scrollbar-thumb {
  background-color: #4B3DDA;
  border-radius: rem(20);
  border: rem(3) solid #262837;
}

body {
  font-family: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1rem;
  font-weight: auto;
}

:root {
  --background-color: #1D1D29;
  --dark: #262837;
  --grey: #90909C;
  --white: #FFFFFF;
  --primary: #4B3DDA;
  --warning: #FA8116;
  --success: #009B9B;
  --info: #C930AE;
}

body {
  background-color: var(--background-color);
  color: var(--white);
  height: 100vh;
}

.container {
  max-width: 58.4375rem;
  margin: 0 auto;
  padding: 0 1.125rem;
}

.h-icon {
  width: 1.5rem;
  height: 1.5rem;
  color: var(--white);
  fill: var(--white);
  cursor: pointer;
}

.upload-icon {
  width: 6rem;
  height: 4.8125rem;
  color: var(--white);
  fill: var(--white);
}

main .container {
  display: flex;
  padding-top: 1.875rem;
  gap: 1.875rem;
}
main .container .content-main {
  flex: 1;
}
main .container .follower-action {
  min-width: 18.3125rem;
}

.header {
  background-color: var(--dark);
  color: var(--white);
}
.header .container {
  height: 3.75rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.25rem;
}
.header .header-logo {
  width: 6.4375rem;
  height: 1.8125rem;
  cursor: pointer;
}
.header .header-search {
  width: 16.75rem;
  height: 2.25rem;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--background-color);
  position: relative;
}
@media (max-width: 640px) {
  .header .header-search {
    display: none;
  }
}
.header .header-search .search-icon {
  position: absolute;
  top: 0;
  left: 0.625rem;
  width: 1rem;
  height: 2.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  cursor: pointer;
}
.header .header-search input {
  width: 100%;
  background-color: transparent;
  padding-left: 1.375rem;
}
.header .header-search input::placeholder {
  color: var(--grey);
}
.header .action {
  display: flex;
  gap: 1.25rem;
}
.header .action .user-action {
  width: 1.5rem;
  height: 1.5rem;
  cursor: pointer;
}
.header .action .user-action img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.i-search {
  color: var(--grey);
}

.post-upload-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.562);
  visibility: hidden;
  transition: 200ms all;
  padding: 0 1.25rem;
}
.post-upload-popup #Close-Post-Upload-Popup {
  width: 1.5rem;
  height: 1.5rem;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  margin: 1.25rem;
}
.post-upload-popup .popup-inner {
  position: relative;
  width: 100%;
}
.post-upload-popup .post-upload {
  width: 30.25rem;
  border-radius: 0.75rem;
  background-color: var(--background-color);
}
.post-upload-popup .post-upload--header {
  padding: 0.625rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 0.0625rem solid var(--dark);
}
.post-upload-popup .post-upload--header h5 {
  font-size: 1rem;
  font-weight: 600;
  line-height: 1.5rem;
  text-align: center;
}
.post-upload-popup .post-upload--content {
  padding: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 30rem;
}
.post-upload-popup .post-upload--content h1 {
  font-size: 1.375rem;
  line-height: 1.625rem;
  font-weight: 300;
  margin-bottom: 0.875rem;
  margin-top: 1rem;
}
.post-upload-popup .post-upload--content .upload-file .text {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 0.875rem;
  font-weight: 600;
  padding: 0.3125rem 0.5625rem;
  text-align: center;
  background-color: var(--primary);
  border: 0.0625rem solid var(--primary);
  border-radius: 0.25rem;
  transition: 200ms background-color;
}
.post-upload-popup .post-upload--content .upload-file .text:hover {
  background-color: rgba(74, 61, 218, 0.315);
}
.post-upload-popup .post-upload--content .upload-file .text::before {
  content: attr(data-invalid);
}
.post-upload-popup .post-upload--content .upload-file input {
  display: none;
}
.post-upload-popup .post-upload--content .upload-file input:valid + .text {
  border-color: var(--success);
  background-color: #4eeb3234;
  border: 0.0625rem solid var(--success);
}
.post-upload-popup .post-upload--content .upload-file input:valid + .text::before {
  content: attr(data-valid);
}